<template>
  <el-dialog
    width="1000px"
    :title="title"
    :visible="value"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-row>
      <el-form ref="form" :model="form" :rules="rules" inline @submit.native.prevent>
        <el-form-item label="" prop="needDate">
          <el-date-picker v-model="form.needDate" align="center" type="date" value-format="yyyy-MM-dd" style="width: 178px;" placeholder="" />
        </el-form-item>
        <el-row>
          <table-com
            ref="baseTableRef"
            :loading="false"
            :table-height-type="490"
            :selection="false"
            row-key="id"
            :table-column-sort="false"
            :table-data="form.detailList"
            :column-list="columnList"
            :operation-width="120"
            :show-operate="true"
            :refresh-key="refreshKey"
            @rowClick="handleRowClick"
          >
            <template slot-scope="scope">
              <el-button type="danger" circle icon="el-icon-minus" @click="handleDelete(scope.index)" />
            </template>
          </table-com>
        </el-row>
      </el-form>
    </el-row>
    <div slot="footer">
      <el-button type="primary" @click="handleConfirm('form')">{{ loading ? ' ...' : ' ' }}</el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import { addSubOrderApi } from '@/apis/modules/out-bound/material-requisition'

const INIT_FORM = () => ({
  needDate: '',
  detailList: []
})

export default {
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      loading: false,
      rules: {
        needDate: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      columnList: [
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: 'SAP',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'needNum',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          setMin: (row) => 0,
          setPrecision: (row) => 3,
          handleChange: _.throttle((row) => {
          }, 1000),
          showOverflowTooltip: false,
          setMax: (row) => row.needMaxNum
        },
        {
          module: 'word',
          prop: 'needDate',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      refreshKey: 1,
      form: INIT_FORM()
    }
  },
  dicts: [],
  watch: {
  },
  methods: {
    // 
    initSelect() {
      this.form.needDate = this.getCurrentDate()
      this.form.detailList = this.extraInfo.selectedList.map(item => {
        const num = item.needNum - item.splitNum
        return {
          ...item,
          needNum: num > item.inventoryNum ? item.inventoryNum : num,
          needMaxNum: num > item.inventoryNum ? item.inventoryNum : num
        }
      })
    },
    getCurrentDate() {
      const date = new Date()
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
        d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
      }
      return `${formatObj.y}-${formatObj.m}-${formatObj.d}`
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
    },
    handleDelete(index) {
      this.form.detailList.splice(index, 1)
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return false
        const info = this.form.detailList.find(item => item.needNum <= 0)
        if (info) {
          this.$message({
            message: `[` + info.materialCode + `]0`,
            type: 'error'
          })
          return
        }
        this.loading = true
        addSubOrderApi({
          ...this.form,
          requisitionNumber: this.extraInfo.billNumber
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: `${this.title}`,
              type: 'success'
            })
            this.$emit('submit')
            this.beforeClose()
          }
          this.loading = false
        }).catch(err => {
          console.log(err)
          this.loading = false
        })
      })
    },
    beforeClose(done) {
      this.form = INIT_FORM()
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
