import { queryCodeDictDetailApi } from '@/apis/modules/common'
import { $dictionaryMap, $dictionaryUrlMap } from '@/utils/dictionary'

/*
*   dictCode
*/
export const dictCodeMixins = {
  data() {
    return {
      sysDictCodeMixins: [],
      lmsDictCodeMixins: [],
      emosDictCodeMixins: [],
      wmsDictCodeMixins: [],
      dictCodeOptsMixins: {}
    }
  },
  mounted() {
    this.initDictCodeMixins()
  },
  methods: {
    initDictCodeMixins() {
      // 
      Promise.all(this.sysDictCodeMixins.map(item => this.getDictCodeMixins(item, 'sys'))).then(res => {
        res.forEach(dictItme => this.$set(this.dictCodeOptsMixins, dictItme.dictName, dictItme.result))
      })
      // lms
      Promise.all(this.lmsDictCodeMixins.map(item => this.getDictCodeMixins(item, 'lms'))).then(res => {
        res.forEach(dictItme => this.$set(this.dictCodeOptsMixins, dictItme.dictName, dictItme.result))
      })
      // eoms
      Promise.all(this.emosDictCodeMixins.map(item => this.getDictCodeMixins(item, 'emos'))).then(res => {
        res.forEach(dictItme => this.$set(this.dictCodeOptsMixins, dictItme.dictName, dictItme.result))
      })
      // wms
      Promise.all(this.wmsDictCodeMixins.map(item => this.getDictCodeMixins(item, 'wms'))).then(res => {
        res.forEach(dictItme => this.$set(this.dictCodeOptsMixins, dictItme.dictName, dictItme.result))
      })
    },
    // 
    getDictCodeMixins(dictName, system) { /**  */
      return new Promise((resolve, reject) => {
        // 
        queryCodeDictDetailApi($dictionaryUrlMap[system], /**  */ {
          name: $dictionaryMap[dictName] /**  */
        }).then(res => {
          const { code, data } = res
          if (!code) {
            resolve({ dictName, result: data.dictDetails })
          }
          reject(res)
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
}
