<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="materialCodeOrName" class="fl">
          <el-input v-model.trim="query.materialCodeOrName" placeholder="" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-form-item prop="materialGroupCode" class="fl">
          <el-input v-model.trim="query.materialGroupCode" placeholder="" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        ></el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        ></el-button>
      </el-form>
    </div>
    <el-row>
      <el-descriptions :column="3">
        <el-descriptions-item label="">{{ baseInfo.date || '---' }}</el-descriptions-item>
        <el-descriptions-item label="">{{ baseInfo.billNumber || '---' }}</el-descriptions-item>
        <el-descriptions-item label="/WBS">{{ baseInfo.contractWbsNumber || '---' }}</el-descriptions-item>
      </el-descriptions>
    </el-row>
    <el-row style="text-align: left;">
      <el-button
        class="filter-item"
        type="primary"
        @click="addCurrentForm"
      ></el-button>
      <el-button
        class="filter-item"
        type="primary"
        :disabled="!tableSelection.length"
        @click="addChildrenForm"
      ></el-button>
      <el-button class="filter-item" type="success" icon="el-icon-download" @click="downloadTemplate"></el-button>
      <form-upload-file
        ref="uploadCom"
        style="margin: 0 10px 0 10px;"
        accept=".xls,.xlsx"
        :url="`/outWarehouse/wmsBillMaterialRequisitionDetail/importExpModel/${params.billNumber}`"
        btn-content=""
        btn-icon="el-icon-upload"
        :limit="99999"
        :show-file-list="false"
        @openLoading="handleOpenUploadLoading"
        @closeLoading="handleCloseUploadLoading"
        @uploadFileSuccess="handleUploadSuccess"
      />
      <el-button
        class="filter-item"
        type="danger"
        :disabled="!tableSelection.length"
        @click="batchDelete"
      ></el-button>
    </el-row>
    <el-row>
      <table-com
        :ref="refName"
        :class="{'baseTable': tableData.length}"
        :row-key="rowKey"
        :table-height-type="530"
        :selection="true"
        :selection-fixed="false"
        :table-selection="tableSelection"
        :loading="loading"
        :index="true"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="170"
        :show-operate="true"
        :expand-list="expandList"
        :cell-style="handleCellStyle"
        :refresh-key="refreshKey"
        :selectable="selectable"
        @rowClick="handleRowClick"
        @refresh="searchInit"
        @selectionChange="selectionChange"
        @dynamicRow="getDynamicRow"
      >
        <!--  -->
        <template slot-scope="scope">
          <el-button type="danger" :disabled="scope.row.splitNum > 0" @click="handleDeleteMaterial(scope.row)"></el-button>
          <el-button type="primary" :disabled="scope.row.splitNum >= scope.row.needNum" @click="editMaterialForm(scope.row)"></el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <!--  -->
    <new-child-order-dialog
      v-model="childVisible"
      title=""
      :extra-info="{selectedList: tableSelection,billNumber: params.billNumber }"
      @submit="handleChildConfirm"
    />
    <!--  -->
    <new-current-order-dialog
      v-model="currentVisible"
      :extra-info="{forBusiness: 'billReserved', billNumber: params.billNumber}"
      @submit="init"
    />
    <!--  -->
    <edit-material-info
      v-model="editVisible"
      :dialog-form="dialogForm"
      @submit="init"
    />
  </div>
</template>

<script>
import { getList1Api, getDetail1Api, delete1Api, batchDeleteApi } from '@/apis/modules/out-bound/material-requisition'
import NewChildOrderDialog from './NewChildOrderDialog'
import NewCurrentOrderDialog from './NewCurrentOrderDialog'
import paginationPage from '@/mixins/paginationPage'
import { dictCodeMixins } from '@/mixins/dictionary'
import { formUploadFilesMixins } from '@/mixins/upload'
import EditMaterialInfo from './EditMaterialInfo'

export default {
  components: {
    NewChildOrderDialog,
    NewCurrentOrderDialog,
    EditMaterialInfo
  },
  mixins: [paginationPage, dictCodeMixins, formUploadFilesMixins],
  props: {
    viewType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: {
        materialCodeOrName: '',
        materialGroupCode: ''
      },
      currentVisible: false,
      editVisible: false,
      dialogForm: {},
      tableSelection: [],
      params: '', // 
      loading: false,
      baseInfo: {},
      childVisible: false,
      refName: 'baseTableRef',
      expandList: [],
      refreshKey: 0,
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: 'SAP',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: true,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'needNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'needDate',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      rowKey: 'id'
    }
  },
  watch: {
    tableSelection: {
      handler(selections) {
        // 
        if (selections.length <= 1) return
        selections.forEach(item => {
          const warehouseSapName = this.tableSelection[0].warehouseSapName
          const flag = item.needNum - item.splitNum !== 0 && item.warehouseSapName === warehouseSapName
          if (!flag) {
            this.$nextTick(() => {
              this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(item, false)
            })
          }
        })
      },
      deep: true
    },
    viewType: {
      handler(val) {
        val === '0' && (this.init(), this.getDetail())
      }
    }
  },
  mounted() {
    this.params = this.$route.params
    this.init()
    this.getDetail()
  },
  methods: {
    init() {
      const params = {
        ...this.query,
        requisitionNumber: this.params.billNumber || '',
        page: {
          ...this.pageInfo
        }
      }
      this.loading = true
      return new Promise((resolve, reject) => {
        getList1Api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
            // 
            const rowKey = this.rowKey
            const ids = this.tableSelection.map(i => i[rowKey])
            this.tableData.forEach(row => {
              function check(row) {
                const id = row[rowKey]
                return ids.some(i => i === id)
              }
              if (check(row)) {
                this.$nextTick(() => {
                  this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                })
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 
    getDetail() {
      return new Promise((resolve, reject) => {
        getDetail1Api({
          billNumber: this.params.billNumber || ''
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseInfo = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    addChildrenForm() {
      this.childVisible = true
    },
    // 
    batchDelete() {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          const params = {
            ids: this.tableSelection.map(c => c.id)
          }
          batchDeleteApi({
            ...params
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '',
                message: ``
              })
              this.$nextTick(() => {
                this.tableSelection.forEach(item => {
                  this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(item, false)
                })
              })
              this.init()
            }
          }).catch(err => {
            console.log('err: ', err)
          })
        })
        .catch((e) => {
          console.log(e)
          this.$notify.info({
            title: '',
            message: ''
          })
        })
    },
    // 
    handleChildConfirm() {
      this.resetSelect()
      this.init()
    },
    // 
    addCurrentForm() {
      this.currentVisible = true
    },
    // 
    editMaterialForm(item) {
      this.editVisible = true
      this.dialogForm = { ...item }
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 
    resetSelect() {
      this.tableSelection = new Array(0)
    },
    // 
    selectable(row, index) {
      // sap
      if (this.tableSelection.length) {
        const warehouseSapName = this.tableSelection[0].warehouseSapName
        return row.needNum - row.splitNum > 0 && row.warehouseSapName === warehouseSapName
      }
      return row.needNum - row.splitNum > 0
    },
    // loading
    handleOpenUploadLoading() {
      this.$emit('changeUploadLoading', true)
    },
    // loading
    handleCloseUploadLoading() {
      this.$emit('changeUploadLoading', false)
    },
    // 
    handleUploadSuccess() {
      this.init()
      this.$notify.success({
        title: '',
        message: ''
      })
    },
    // Tip 
    handleCellStyle({ row }) {
      const cellStyle = {
      }
      return cellStyle
    },
    // Tip 
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
    },
    // Tip 
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    getDynamicRow(item) {
    },
    // 
    downloadTemplate() {
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/outWarehouse/wmsBillMaterialRequisitionDetail/downloadImportTemplate`)
    },
    handleDeleteMaterial(item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          const params = {
            id: item.id
          }
          delete1Api({
            ...params
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '',
                message: ``
              })
              this.$nextTick(() => {
                this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(item, false)
              })
              // this.tableSelection = this.tableSelection.filter(c => c[this.rowKey] !== item[this.rowKey])
              this.init()
            }
          }).catch(err => {
            console.log('err: ', err)
          })
        })
        .catch((e) => {
          console.log(e)
          this.$notify.info({
            title: '',
            message: ''
          })
        })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .baseTable .el-checkbox__input.is-disabled .el-checkbox__inner {
  background-color: #726f72;
  border-color: #DCDFE6;
}
</style>
