<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="subNumber" class="fl">
          <el-select v-model="query.subNumber" filterable clearable placeholder="" style="width: 178px;" class="filter-item">
            <el-option
              v-for="(item,index) in SonPullList"
              :key="index"
              :label="item.subNumber"
              :value="item.subNumber"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        ></el-button>
        <!-- <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        ></el-button> -->
      </el-form></div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="350"
        :table-column-sort="false"
        :show-operate="true"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <template v-if="scope.row.status === 1">
            <el-button type="primary" @click="handle(scope.row, 'Submit')"></el-button>
            <el-button type="warning" @click="handleModify(scope.row)"></el-button>
            <el-button type="danger" @click="handle(scope.row, 'Delete')"></el-button>
          </template>
          <el-button type="primary" @click="handleDetail(scope.row)"></el-button>
          <el-button type="primary" @click="handlePrint(scope.row)"></el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>
<script>
import { getList2Api, delete2Api, submit2Api, print2Api, getSubOrderPullList2Api } from '@/apis/modules/out-bound/material-requisition'
import paginationPage from '@/mixins/paginationPage'
import { dictCodeMixins } from '@/mixins/dictionary'
export default {
  components: {},
  mixins: [paginationPage, dictCodeMixins],
  props: {
    viewType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: {
        subNumber: ''
      },
      params: {}, // 
      loading: false,
      tableData: [],
      SonPullList: [],
      dialogType: '',
      columnList: [
        {
          module: 'word',
          prop: 'subNumber',
          label: '',
          align: 'center',
          width: '',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'createUserName',
          label: '',
          align: 'center',
          minWidth: '',
          width: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'createDate',
          label: '',
          align: 'center',
          minWidth: '',
          width: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'printTimes',
          label: '',
          align: 'center',
          minWidth: '',
          width: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {
    //   
    pageType() {
      return this.$route.name.replace('OutBoundReservationForm', '').toLowerCase()
    }
  },
  watch: {
    viewType: {
      handler(val) {
        val === '1' && (this.init(), this.getSonPull())
      }
    }
  },
  mounted() {
    this.params = this.$route.params
    this.init()
    this.getSonPull()
  },
  methods: {
    init() {
      return new Promise((resolve, reject) => {
        const { query } = this
        this.loading = true
        getList2Api({
          ...query,
          requisitionNumber: this.params.billNumber,
          page: {
            ...this.pageInfo
          }
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 
    handlePrint(item) {
      const { requisitionNumber, subNumber } = item
      const params = { requisitionNumber, subNumber }
      return new Promise((resolve, reject) => {
        print2Api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            window.open(process.env.VUE_APP_BASE_API_WMS + data)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    getSonPull() {
      return new Promise((resolve, reject) => {
        getSubOrderPullList2Api({
          requisitionNumber: this.params.billNumber
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.SonPullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    handleModify(item) {
      this.$router.push({
        name: 'OutBoundMaterialRequisitionSubOrderEdit',
        query: {
          billNumber: item.subNumber
        }
      })
    },
    //  
    handle(item, name) {
      const obj = {
        Submit: { api: submit2Api, tip: '' },
        Delete: { api: delete2Api, tip: '' }
      }
      this.$confirm(`${obj[name]['tip']}`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        obj[name]['api']({
          ...name === 'Delete' && { id: item.id },
          ...['Submit'].includes(name) && { subNumber: item.subNumber },
          ...['Withdraw'].includes(name) && { billNumber: item.subNumber }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '',
              message: ''
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    // 
    handleDetail(item) {
      this.$router.push({
        name: 'OutBoundMaterialRequisitionSubOrderDetail',
        query: {
          billNumber: item.subNumber
        }
      })
    },
    // Tip 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
