<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="100px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="SAP" prop="warehouseId">
          <el-select
            v-model="form.warehouseId"
            placeholder=""
            style="width: 178px;"
            @change="handleSelectChange"
          >
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.warehouseSapName"
              :value="item.warehouseSapId"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="materialCode">
          <el-input v-model.trim="form.materialCode" :disabled="true" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="materialName">
          <el-input v-model.trim="form.materialName" :disabled="true" placeholder="" style="width: 465px;" />
        </el-form-item>
        <el-form-item label="" prop="materialAssessType_dictText">
          <el-input v-model.trim="form.materialAssessType_dictText" :disabled="true" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="unitName">
          <el-input v-model.trim="form.unitName" :disabled="true" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="inventoryNum">
          <el-input v-model.trim="form.inventoryNum" :disabled="true" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="needNum">
          <el-input-number v-model.trim="form.needNum" :min="0.001" :max="Infinity" :precision="3" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="needDate">
          <el-date-picker
            v-model="form.needDate"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            :disabled="true"
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="materialGroupCode">
          <el-input v-model.trim="form.materialGroupCode" :disabled="true" placeholder="" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="" prop="materialGroupDesc">
          <el-input v-model.trim="form.materialGroupDesc" :disabled="true" placeholder="" style="width: 465px;" />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? ' ...' : ' ' }}</el-button>
        <el-button @click="beforeClose"> </el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { getEdit1Api } from '@/apis/modules/out-bound/material-requisition'
import { queryInventoryNumByMaterialCodeApi } from '@/apis/modules/warehouse/inventory'

const INIT_FORM = () => ({
  warehouseId: '', // sapid
  materialCode: '', // 
  materialName: '', // 
  materialAssessType_dictText: '', // 
  unitName: '', // 
  inventoryNum: '', // 
  needNum: '', // 
  needDate: '', // 
  materialGroupCode: '', // 
  materialGroupDesc: '' // 
})

export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    billNumber: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      rules: {
        needNum: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      warehousePullList: [] // 
    }
  },
  computed: {
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        this.form = { ...value }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          getEdit1Api({
            ...this.form
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {
      this.getWarehousePullList()
    },
    // 
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        queryInventoryNumByMaterialCodeApi({
          materialCode: this.form.materialCode
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    handleSelectChange(bindVal) {
      const obj = this.warehousePullList.find(f => f.warehouseSapId === bindVal)
      if (obj) {
        this.form.inventoryNum = obj.inventoryNum
        return
      }
      this.form.inventoryNum = 0
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 640px;
  }
  .drawer-footer{
    text-align: right;
  }
  .x-suffix {
    display: none;
    color: #909399;
  }
  .control-x {
    // @include nowrap-ellipsis;
    position: relative;
    &:hover {
      .x-suffix {
        display: block;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }
</style>

