<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50%"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="filterItem" :model="form" inline :rules="rules" label-width="100px" class="top-filter clearfix" @submit.native.prevent>
      <el-form-item prop="materialCode">
        <el-input v-model.trim="form.materialCode" placeholder="" style="width: 178px;" class="filter-item" @keyup.enter.native="handleSearch" />
      </el-form-item>
      <el-button
        class="filter-item"
        type="success"
        icon="el-icon-search"
        @click="handleSearch"
      ></el-button>
      <el-button
        class="filter-item"
        type="warning"
        icon="el-icon-refresh-left"
        @click="resetForm('filterItem')"
      ></el-button>
    </el-form>
    <el-row>
      <table-com
        :ref="refName"
        :ref-name="tableRef"
        :loading="loading"
        :table-height-type="725"
        :selection="false"
        :row-key="rowKey"
        highlight-current-row
        :cell-style="handleCellStyle"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :show-operate="true"
        :refresh-key="refreshKey"
        @rowClick="handleRowClick"
        @refresh="handleRefresh"
      >
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row, scope.index)"></el-button>
        </template>
      </table-com>
    </el-row>
    <div class="drawer-footer">
      <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? ' ...' : ' ' }}</el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import _ from 'lodash'
import { _debounce } from '@/utils'
import { batchAddMaterialApi, queryMaterialApi } from '@/apis/modules/out-bound/material-requisition'

const INIT_FORM = () => ({
  materialCode: ''
})

export default {
  components: {
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      currentDate: '',
      loading: false,
      rowKey: 'id1',
      refName: 'baseTableRef',
      tableRef: 'tableRef',
      refreshKey: 1,
      rules: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: 'SAP',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'select',
        //   prop: 'dict',
        //   label: '',
        //   align: 'center',
        //   minWidth: '120',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headAlign: 'center',
        //   optionLabel: 'name', //  label key
        //   optionValue: 'code', //  value key
        //   /*
        //   *  @params setDisabled 
        //   *  
        //   */
        //   setDisabled: () => {
        //     return false
        //   },
        //   /*
        //   *  @params getOptions 
        //   *  
        //   */
        //   getOptions: (item) => {
        //     return []
        //     // return this.dict['sys_user_type']
        //   }
        // },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'needNum',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          isShow: (item) => {
            return true
          },
          getDisplay: (item) => {
            return ''
          },
          disabled: (item) => {
            return false
          },
          setMin: (item) => 0,
          setMax: (item) => Infinity,
          setPrecision: (item) => 3,
          handleChange: _.throttle((item) => {
          }, 1000),
          showOverflowTooltip: false
        },
        {
          module: 'date',
          prop: 'needDate',
          label: '',
          align: 'center',
          width: '',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          inputWidth: '130',
          valueFormat: 'yyyy-MM-dd',
          placeholder: '',
          clearable: true,
          isShow: (item) => {
            return true
          },
          getDisplay: (item) => {
            return ''
          },
          setDisabled: (item) => {
            return false
          },
          handleChange: (item) => null,
          showOverflowTooltip: false
        }
      ]
    }
  },
  computed: {
  },
  watch: {
  },
  methods: {
    initSelect() {
      const date = new Date()
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
        d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
      }
      this.currentDate = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
    },
    handleSearch: _debounce(function() {
      if (!this.form.materialCode) {
        return
      }
      const isExist = this.tableData.some(c => c.materialCode === this.form.materialCode)
      if (isExist) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      return new Promise((resolve, reject) => {
        const params = {
          ...this.form
        }
        queryMaterialApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              if (Array.isArray(data) && data.length) {
                const newData = data.map(c => ({
                  ...c,
                  materialId: c.id, materialCode: c.code, materialName: c.name, assessType_dictText: c.assessType_dictText, inventoryNum: c.inventoryNum, needNum: 0, needDate: this.currentDate, requisitionNumber: this.extraInfo.billNumber
                }))
                this.tableData = [...this.tableData, ...newData]
                this.tableData.forEach((c, i) => {
                  c.id1 = `${c.id}-${i}`
                })
                this.scrollToRow(newData[0])
              }
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    }, 800, false),
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.form = INIT_FORM()
    },
    handleRefresh() {},
    // 
    handleCellStyle({ row }) {
      const cellStyle = {
        backgroundColor: row.materialCode === this.form.materialCode ? '#409EFF' : ''
      }
      return cellStyle
    },
    scrollToRow(row) {
      this.$nextTick(() => {
        const table = this.$refs[this.refName].$refs[this.tableRef]
        if (table) {
          const rows = table.$el.querySelectorAll('.el-table__body tr')
          // const targetRow = rows[this.tableData.findIndex(c => c.materialCode === row.materialCode)]
          const targetRow = rows[this.tableData.findIndex(c => c[this.rowKey] === row[this.rowKey])]
          if (targetRow) {
            // 
            targetRow.scrollIntoView(true)
          }
        }
      })
    },
    handleDelete(item, index) {
      this.$confirm(`, ?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        // this.tableData = this.tableData.filter(c => c.materialId !== item.materialId)
        this.tableData.splice(index, 1)
        this.tableData[index] && this.scrollToRow(this.tableData[index])
        this.$notify.success({
          title: '',
          message: ''
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    handleRowClick() {},
    handleConfirm() {
      const isNoExist = this.tableData.some(c => !c.needDate || !c.needNum)
      if (isNoExist) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      return new Promise((resolve, reject) => {
        batchAddMaterialApi([...this.tableData])
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '',
                message: ''
              })
              this.$emit('submit')
              this.beforeClose()
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    beforeClose(done) {
      this.resetForm('filterItem')
      this.tableData = []
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
  }
  ::v-deep .el-drawer {
    // width: 800px;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .drawer-footer{
    text-align: right;
  }
</style>

